@import url("https://fonts.googleapis.com/css?family=IBM+Plex+Mono|Poppins:800");
.event{
margin-top: -10px;
background: white !important;
}

.ended__events>ul {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  justify-content: center; 
  padding: 0;
}
.events__main{
  padding-left: 250px;
}
.ended__events,.upcoming__events{
  z-index: 1000000;
}
.ended__events{
  background: linear-gradient(#a1c4fd,#c2e9fb);
  padding-top: 60px;
}
.upcoming__events{
  background-color: #A9C9FF;
  background-image: linear-gradient(180deg, #A9C9FF 0%, #FFBBEC 100%);
}
.upcoming__events__card{
  padding-top: 60px;
}
.event__card {
  border-radius: 20px;
  padding: 13px;
  max-width: 318px;
  width: 100%;
  background: linear-gradient(-120deg, #EC51DE 0%, #53A0FD 60%, #3023AE 100%);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.card__filter {
  height: 212px;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  overflow: hidden;
  position: relative;
  object-fit: cover;
}
.card__filter::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  /* mix-blend-mode: multiply;
  background: linear-gradient(-150deg, #EB0EC7 0%, #1897E1 100%); */
  z-index: 1;
}
.card__photo {
  width: 292px;
  border-radius: 20px;
  height: 500px;
  /* -webkit-filter: grayscale(50%);
  filter: grayscale(50%); */
}
.card__container {
  background: #fff;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 1em;
  box-sizing: border-box;
}
.event__card h4 {
  font-family: "Poppins", sans-serif;
  font-weight: regular;
  font-size: 20px;
  color: #405CD2;
  letter-spacing: -1.06px;
  margin: 0 0.4em 0.4em 0;
}
.event__card time, .card p, .card .card__location {
  font-family: "IBM Plex Mono", sans-serif;
  font-weight: regular;
  font-size: 14px;
  color: #535353;
  margin: 0.18em;
}
.card__location {
  text-decoration-color: #EB0EC7;
  -webkit-text-decoration-color: #EB0EC7;
  transition: all 250ms ease;
  max-width: 13.3em;
}
.card__location:hover {
  background-color: rgba(235, 14, 199, 0.2);
  text-decoration-color: rgba(235, 14, 199, 0.2);
}
.card__buttons {
  display: flex;
  align-items: center;
  width: 100%;
  margin: 1.5em 0 0.5em 0;
  flex-direction: row;
}
.card__buttons .btn {
  font-family: "IBM Plex Mono", sans-serif;
  font-weight: regular;
  font-size: 15px;
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 1px;
  text-align: center;
  display: block;
  flex: 1 1 100%;
  color: #fff;
  padding: 1em;
}
.card__buttons .btn:hover {
  -webkit-animation: gradient 1.3s ease infinite;
  -moz-animation: gradient 1.3s ease infinite;
  animation: gradient 1.3s ease infinite;
}
.card__buttons .btn:hover i {
  -webkit-animation: levitate 1.3s ease infinite;
  -moz-animation: levitate 1.3s ease infinite;
  animation: levitate 1.3s ease infinite;
}
.card__buttons .primary {
  position: relative;
  background-image: linear-gradient(to right, #1897E1, #3022AD);
  background-size: 150% 150%;
  transition: all 0.4s ease;
  z-index: 1;
}
.card__buttons .secondary {
  position: relative;
  background-image: linear-gradient(to right, #CC19BF, #7623A2);
  background-size: 150% 150%;
  transition: all 0.4s ease;
  z-index: 1;
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
@keyframes levitate {
  0% {
    padding-left: 0px;
  }
  50% {
    padding-left: 5px;
  }
  100% {
    padding-left: 0px;
  }
}
@media (max-width: 400px) {
  .fas {
    display: none;
  }

  .card__buttons {
    display: inline;
  }
}
.events__main ul {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  justify-content: center;
  padding: 0;
}

.events__main li {
  margin: 1em;
}

/* aside */

aside {
  position: fixed;
  color: #fff;
  display: flex;
  flex-direction: column;
  width: 250px;
  padding-left: 20px;
  height: 100vh;
  background: #f4f5f7;
  border-top-right-radius: 80px;
}

aside a {
  font-size: 17px;
  color: black;
  display: block;
  padding: 12px;
  padding-left: 30px;
  text-decoration: none;
  -webkit-tap-highlight-color:transparent;
}

aside a:hover {
  color: #fff;
  background: #3f5efb;
  outline: none;
  position: relative;
  background-color: #3f5efb;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
}

aside a i {
  margin-right: 5px;
}

aside a:hover::after {
  content: "";
  position: absolute;
  background-color: transparent;
  bottom: 100%;
  right: 0;
  height: 35px;
  width: 35px;
  border-bottom-right-radius: 18px;
  box-shadow: 0 20px 0 0 #3f5efb;
}

aside a:hover::before {
  content: "";
  position: absolute;
  background-color: transparent;
  top: 38px;
  right: 0;
  height: 35px;
  width: 35px;
  border-top-right-radius: 18px;
  box-shadow: 0 -20px 0 0 #3f5efb;
}

aside p {
  margin: 0;
  padding: 40px 0;
  color: black;
  text-decoration: underline;
  font-family:'Times New Roman', Times, serif;
  font-size: 30px;
}

@media(max-width:800px){
  aside{
    display: none;
  }
  .events__main{
    padding-left: 0px;
  }
}