.contact-connect{
  padding-bottom: 30px;
    margin-top: 130px;
    margin-left: auto;
    margin-right: auto;
}
.contact-section-header {
    text-align: center;
    margin: 0 auto;
    padding: 40px 0;
    letter-spacing: 6px;
    color: #000;
    text-decoration: underline;
    font: 300 60px 'Oswald', sans-serif !important;
    text-decoration-color: chocolate !important;
  }

  .contact-connect span{
    color: chocolate;
  }
  .contact-connect p{
    font-size: 25px;
    font: 'Oswald', sans-serif;
  }
  .phone__code{
    background-color: grey;
    color: white !important;
  }
  @media(max-width:800px){
    .contact-connect p{
      font-size: 20px;
    }
  }