.event-dialog-title {
    text-align: center;
  }
  .event-dialog-title > img {
    height: 250px;
    width: 250px;
  }
  .event-dialog-title > h2 {
    text-decoration: underline;
  }
  .event-dialog-title > hr {
    height: 20px;
    width: 100%;
    background-image: radial-gradient(
      farthest-side at 50% -50%,
      rgba(0, 0, 0, 0.5),
      rgba(0, 0, 0, 0)
    );
    position: relative;
  }
  
  .event-dialog-title > hr::before {
    height: 1px;
    position: absolute;
    top: -1px;
    left: 0;
    right: 0;
    background-image: linear-gradient(
      90deg,
      rgba(0, 0, 0, 0),
      rgba(0, 0, 0, 0.75) 50%,
      rgba(0, 0, 0, 0)
    );
  }
  