.grid {
  width: 90vw;
  margin: 10vmin auto;
  justify-content: center;
  display: flex;
  flex-wrap: wrap;
  transform-style: preserve-3d;
  box-shadow: 0px 0px 8vmin 4vmin rgba(0, 0, 0, 0.1),
    inset 0px 0px 8vmin 4vmin rgba(0, 0, 0, 0.1);
  perspective: 2000;
}

.item-box {
  position: relative;
  width: 26.66667vmin;
  height: 26.66667vmin;
  transform-style: preserve-3d;
  cursor: pointer;
  transition: all 0.225s cubic-bezier(0.65, 0.05, 0.36, 1) 0.225s;
}

