.item-box:hover {
  transform: scale(1.25, 1.25);
  box-shadow: 0px 0px 16px 8px rgba(0, 0, 0, 0.2);
}
.item-box:hover .puzzle {
  transform: translateX(0) translateY(0) translateZ(1px) rotateX(0) rotateY(0);
  opacity: 1;
}
.item-box .item-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform-style: preserve-3d;
}

.piece {
  position: absolute;
  width: 13.33333vmin;
  height: 13.33333vmin;
  transform-style: preserve-3d;
}
.piece:nth-child(1) {
  transform: translateX(-100%) translateY(-100%) rotate(0deg);
}
.piece:nth-child(1) .puzzle:before {
  background-position: 0% 0%;
  transform: rotate(0deg);
}
.piece:nth-child(2) {
  transform: translateX(0%) translateY(-100%) rotate(90deg);
}
.piece:nth-child(2) .puzzle:before {
  background-position: 100% 0%;
  transform: rotate(-90deg);
}
.piece:nth-child(3) {
  transform: translateX(0%) translateY(0%) rotate(180deg);
}
.piece:nth-child(3) .puzzle:before {
  background-position: 100% 100%;
  transform: rotate(-180deg);
}
.piece:nth-child(4) {
  transform: translateX(-100%) translateY(0%) rotate(270deg);
}
.piece:nth-child(4) .puzzle:before {
  background-position: 0% 100%;
  transform: rotate(-270deg);
}

.puzzle {
  position: absolute;
  width: 17.17505vmin;
  height: 17.17505vmin;
  background-color: #000;
  -webkit-clip-path: url(#puzzle);
  clip-path: url(#puzzle);
  opacity: 0.8;
  transform: translateX(-22.368%) translateY(-22.368%) translateZ(0px)
    rotateX(0.5turn) rotateY(0.5turn);
  transition: all 0.45s cubic-bezier(0.65, 0.05, 0.36, 1);
}
.puzzle:before {
  content: "";
  position: absolute;
  width: 17.17505vmin;
  height: 17.17505vmin;
  background-size: 155.264%;
}

@keyframes init {
  from {
    transform: translateX(0) translateY(0) translateZ(1px) rotateX(0) rotateY(0);
  }
}

@media (max-width: 800px) {
  .item-dissapear {
    display: none;
  }
  .grid {
    width: 80vmin;
  }
  .gallery-container {
    background: none;
  }
  .item-box:nth-child(5) .puzzle:before {
    background-image: url(../../static/media/logo2.cb2913a8.png);
  }
}

