.footer {
  padding-top: 30px;
  padding-bottom: 30px;
  background: #f4f5f7 !important;
}
.footer > ul {
  display: flex;
  justify-content: center;
}

.footer > ul > li {
  list-style: none;
}

.footer>h5{
  text-align: center;
}
.footer > ul > li > a {
  width: 80px;
  height: 80px;
  background-color: #fff;
  text-align: center;
  line-height: 80px;
  font-size: 35px;
  margin: 0 10px;
  display: block;
  border-radius: 50%;
  position: relative;
  overflow: hidden;
  border: 3px solid #fff;
  z-index: 1;
}

.footer > ul > li > a > .icon {
  position: relative;
  color: #262626;
  transition: 0.5s;
  z-index: 3;
}

.footer > ul li a:hover .icon {
  color: #fff;
  transform: rotateY(360deg);
}

.footer > ul li a:before {
  content: "";
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  height: 100%;
  background: #f00;
  transition: 0.5s;
  z-index: 2;
}

.footer > ul li a:hover:before {
  top: 0;
}

.footer > ul li:nth-child(1) a:before {
  background: #3b5999;
}

.footer > ul li:nth-child(2) a:before {
  background: linear-gradient(
    45deg,
    #f09433 0%,
    #e6683c 25%,
    #dc2743 50%,
    #cc2366 75%,
    #bc1888 100%
  );
}

.footer > ul li:nth-child(3) a:before {
  background: red;
}

