:root {
    --timing-anim-sipka: 200ms;
    --main-color-sipka: #020202;
    --hover-color-sipka: orange;
  }

.news_main{
    padding-top: 50px;
}
.news_header>h1{
text-decoration: underline;
text-decoration-color: orange;
}
.news{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}
.news > a {
  text-decoration: none;
  margin: 20px 50px;
}
.event__details > .event__details__info > hr {
  border: 2px solid white;
  width: 2%;
}
.event__details {
  margin: 30px auto;
  height: 50vh;
  width: 300px;
  position: relative;
  background-size: cover;
  transition: all 0.5s;
  overflow: hidden;
  text-align: center;
}
.event__details:hover {
  transform: scale(1.1);
  overflow: auto;
  cursor: pointer;
}

.event__details__info {
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
  color: white;
  position: relative;
  top: 75%;
  transition: all 0.5s;
  height: 100%;
}
.event__details__info > h1 {
  color: white;
}
.event__details__info > p {
  display: none;
}
.event__details:hover > .event__details__info > p {
  display: block;
}

.event__details:hover > .event__details__info {
  top: 0;
}
.left__arrow {
  transform: rotateZ(45deg);
  position: relative;
  right: 0.5%;
}
.right__arrow {
  transform: rotateZ(-45deg);
  position: relative;
  bottom: 2.7vh;
  left: 1%;
}

.event__details:hover > .event__details__info > hr {
  display: none;
}

/* arrow */
.expand-arrow{
    border-radius: 50%;
    border: 2px solid white;
    width: 30px;
    margin-left: auto;
    margin-right: auto;
}
.event__details:hover>.event__details__info>.expand-arrow{
    display: none;
}